
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';

/*
  Generated class for the SocketProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SocketProvider {
  url= 'ws://snaplibs-ws-spring-boot.herokuapp.com/';
  socket;
  dataSubject = new Subject();
  name;
  roomNumber;
  players;
  isHost = false;

  constructor() {
    console.log('Hello SocketProvider Provider');
    this.connect();
  }

  setName (name) {
    this.name = name;
  }

  setRoomNumber (number) {
    this.roomNumber = number;
  }

  setPlayers (players) {
    this.players = players;
  }

  getStateSubject () {
    return this.dataSubject;
  }

  connect() {
    this.socket = new WebSocket(`${this.url}/game`);
    this.socket.onmessage = this.handleMessage.bind(this);
  }

  disconnect() {
    if (this.socket !== null) {
      this.socket .close();
    }
    console.log("Disconnected");
  }

  handleMessage (data) {
    console.log('received: ', data);
    this.dataSubject.next(JSON.parse(data.data));
  }

  sendMessage (message) {
    console.log('message sent: ', message);
    this.socket.send(JSON.stringify(message));
  }

}



